.navopen{
    border: solid 20px black;
    -webkit-animation: navslide 0.5s ease-out;
            animation: navslide 0.5s ease-out;
    display:block;
    background-color: rgba(0, 0, 0, 0.746);
    background: rgba(0, 0, 0, 0.746) url(/Alpha/static/media/AlphaNav1.c136c5e0.svg);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: bottom;

    width:100%;
    -webkit-filter:blur(100%);
            filter:blur(100%);
    height:100%;
    z-index: 5;
    margin:0;
    position: fixed;
    left:0;
    top:0;
    right:0;
    transition: 0.5s;
}
.navclose{
    -webkit-animation: navback 1s ease-in-out;
            animation: navback 1s ease-in-out;
    display: none;
}
.navbtn{
    left: 2%;
    top:2%;
    /* position:absolute; */
