    /* background-color: rgba(0, 0, 0, 0); */
    border:0;
    background: url(/Alpha/static/media/navbutton.cf6bf88b.svg);
    background-size: cover;
   
}
.navbtn:hover{
    -webkit-animation: btngrow 1s ease;
            animation: btngrow 1s ease;
}
@-webkit-keyframes btngrow {
    0%{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    50%{
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
}
@keyframes btngrow {
    0%{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    50%{
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
