.inst-card:before{
    position: absolute;
    content: "";
    height: 0;
    border: 2px solid transparent;
    bottom: 0;
    left: 0;
    width: 0;
    box-sizing: border-box;
}
.inst-card:after{
    position: absolute;
    content: "";
    height: 0;
    border: 2px solid transparent;
top:0;
right:0;
    width: 0;
    box-sizing: border-box;
}
.inst-card:hover:before{
    height: 110px;
    width: 487px;
    border: 2px solid white;
    border-right: none;
    border-bottom: none;
    transition: height 0.5s linear,
                width 0.5s linear 0.5s;
}
.inst-card:hover:after{
    height: 110px;
    width: 487px;
    border: 2px solid white;
    border-left: none;
    border-top: none;
    transition: height 0.5s linear,
                width 0.5s linear 0.5s;
}

